import { useEffect, useState } from 'react'
import Button from '../components/button'
import { useStore } from '../App'
import findUnansweredQuestions from '../utils/findUndefined'
import showRequiredMessage from '../utils/showRequiredMessage'

const SectionTwoButtons = ({ setVisibleSection, visibleSection }) => {
  const section1Answers = useStore((state) => state.section1Answers)
  const section2Answers = useStore((state) => state.section2Answers)
  const setRequiredQuestions = useStore(
    (state) => state.setRequiredSection2Questions
  )
  const [showQuestion, setShowQuestion] = useState(false)

  useEffect(() => {
    section1Answers.find(answer => answer.item.showQuestion) && setShowQuestion(true)
  }, [section1Answers])

  return (
    <>
      <Button
        icon="previous"
        variant="outline"
        onClick={() => {
          setVisibleSection(visibleSection - 1)
        }}
      >
        Back
      </Button>
      <Button
        icon="next"
        onClick={async () => {
          const unansweredQuestions =
            showQuestion ? findUnansweredQuestions(section2Answers) 
                : findUnansweredQuestions(section2Answers.filter((x, i) => i !== 1))
          console.log(showQuestion, section2Answers, unansweredQuestions)
          unansweredQuestions.length > 0
            ? showRequiredMessage() && setRequiredQuestions(unansweredQuestions)
            : setVisibleSection(visibleSection + 1)
        }}
      >
        Step 3: Project readiness
      </Button>
    </>
  )
}

export default SectionTwoButtons
