import Button from '../components/button'
import { useStore } from '../App'
import mapAnswersToPostString from '../utils/mapAnswersToPostString'
import findUnansweredQuestions from '../utils/findUndefined'
import showRequiredMessage from '../utils/showRequiredMessage'

const postToFormTools = async (postString, form_id) => {
  const url = 'https://webtools.ec.europa.eu/form-tools/process.php'
  const postStringWithFormId = postString + '&form_tools_form_id=' + form_id
  console.log(postStringWithFormId)
  const response = await fetch(url, {
    method: 'POST',
    mode: 'no-cors',
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    },
    body: postStringWithFormId
  })
  console.log('response from the post ->', response)
}

const SectionThreeButtons = ({ setVisibleSection, visibleSection }) => {
  const section1Answers = useStore((state) => state.section1Answers)
  const section2Answers = useStore((state) => state.section2Answers)
  const section3Answers = useStore((state) => state.section3Answers)
  const setRequiredQuestions = useStore(
    (state) => state.setRequiredSection3Questions
  )

  return (
    <>
      <Button
        icon="previous"
        variant="outline"
        onClick={() => {
          setVisibleSection(visibleSection - 1)
        }}
      >
        Back
      </Button>
      <Button
        icon="next"
        onClick={async () => {
          const unansweredQuestions = findUnansweredQuestions(section3Answers)
          if (unansweredQuestions.length > 0) {
            showRequiredMessage() && setRequiredQuestions(unansweredQuestions)
          } else {
            const form_id = 1806
            const postString = mapAnswersToPostString([
              ...section1Answers,
              ...section2Answers,
              ...section3Answers,
            ])
            postToFormTools(postString, form_id)
            console.log('answers posted->', postString)

            setVisibleSection(visibleSection + 1)
          }
        }
        }
      >
        Results
      </Button>
    </>
  )
}

export default SectionThreeButtons
