const mapAnswersToPostString = (allAnswers, form_id) => {
  const postString = Object.keys(allAnswers)
    .map((key) =>
      allAnswers[key]
        ? `${allAnswers[key].key}=${
            allAnswers[key].item.count ?? (allAnswers[key].item.text || allAnswers[key].item)
          }`
        : null
    )
    .filter((x) => x)
    .join('&')
  const encoded = encodeURI(postString)
  // return encoded + '&form_tools_form_id=1748&form_tools_initialize_form=1'
  return encoded + '&form_tools_form_id=' + form_id
}
export default mapAnswersToPostString
