import { useState } from 'react'
import { useStore } from '../App'
import Question from '../components/question'
import RedStar from '../components/redstar'
import questions from '../data/questions'

const SectionThree = () => {
  const section = 2 // array is zero indexed so 2 is section 3
  const sectionQuestions = questions[section]
  const [showQuestion] = useState([])
  // const requiredQuestions = useStore((state) => state.requiredQuestions)
  const [otherQuestionsToShow, setOtherQuestionsToShow] = useState([])

  const setSection3Answers = useStore((state) => state.setSection3Answers)
  const section3Answers = useStore((state) => state.section3Answers)
  const requiredQuestions = useStore((state) => state.requiredSection3Questions)

  const handleChange = (questionIndex) => (item) => {
    if (item) {
      // checks to see if we need to capture other text and whether the user has selected 'Other'
      const isOtherSelected =
        sectionQuestions[questionIndex].captureOtherText &&
        item.selectedItem.showOtherText

      if (isOtherSelected) {
        // user has selected Other so push this question into the tracking array so the text box will appear
        setOtherQuestionsToShow([...otherQuestionsToShow, questionIndex])
      } else {
        // user hasn't selected Other however if Other was selected previously then check and remove it so that the box disappears
        // TODO: may need to remove the answer as well to clear everything
        if (otherQuestionsToShow.includes(questionIndex)) {
          setOtherQuestionsToShow(
            otherQuestionsToShow.filter((ind) => ind !== questionIndex)
          )
        }
      }

      setSection3Answers(
        {
          item: item.selectedItem,
          key: sectionQuestions[questionIndex].key,
        },
        questionIndex
      )
    }
  }

  const handleOtherTextChange = (questionIndex) => (ev) => {
    const text = ev.target.value

    setSection3Answers(
      {
        item: { ...section3Answers[questionIndex].item, text },
        key: sectionQuestions[questionIndex].key,
      },
      questionIndex
    )
  }

  return (
    <section>
      <div style={{ display: 'flex', justifyContent: 'space-between'}}>
        <h1>Step 3 of 3: Project readiness</h1>
        <span><RedStar/> <i>Required</i></span>
      </div>
      <div className='sub-title'>
        Please enter the details related to your project below. 
        Your answers will help us assess how ready your project might be compared to the Innovation Fund expectations.
      </div>
      {
        section3Answers.filter(x=>!x).length > 0 && <div className="required-field">Fields marked with <RedStar /> are required</div>
      }
      {sectionQuestions.map(({ key, ...question }, questionIndex) => {
        return (
          <Question
            {...question}
            key={`${key}-${questionIndex}`}
            questionIndex={questionIndex}
            section={section}
            handleChange={handleChange(questionIndex)}
            handleOther={handleOtherTextChange(questionIndex)}
            showQuestion={
              showQuestion.includes(questionIndex) ? questionIndex : -1
            }
            showOther={
              section3Answers[questionIndex] &&
              section3Answers[questionIndex].item.showOtherText
            }
            required={
              requiredQuestions.filter((rq) => rq === questionIndex)[0] ===
              questionIndex
            }
            initialSelectedItem={section3Answers[questionIndex]?.item || ''}
            autoFocus={questionIndex === 0}
            answered={!!section3Answers[questionIndex]}
          />
        )
      })}
    </section>
  )
}

export default SectionThree
