import ChevronRight from '../chevron-right'
import styles from './page-header.module.css'

const PageHeader = ({ breadcrumbs, pageTitle, pageSubtitle }) => {
  return (
    <div className={styles.pageheader__wrapper}>
      <div className={`container ${styles.pageheader__container}`}>
        <ol className={styles.breadcrumbs}>
          {breadcrumbs.map((crumb, index) => (
            <li key={`breadcrumb-${index}`} className={styles.crumb}>
              <a href={crumb.href}>{crumb.text}</a>
              {index < breadcrumbs.length - 1 ? <ChevronRight /> : null}
            </li>
          ))}
        </ol>
        <h1 className={styles.title}>{pageTitle}</h1>
        <p className={styles.subtitle}>{pageSubtitle}</p>
      </div>
    </div>
  )
}

export default PageHeader
