import { useEffect, useState } from 'react'
import { useStore } from '../App'
import Question from '../components/question'
import RedStar from '../components/redstar'
import questions from '../data/questions'

const SectionTwo = () => {
  const section = 1
  const sectionQuestions = questions[section]
  const [showQuestion, setShowQuestion] = useState([])
  const requiredQuestions = useStore((state) => state.requiredSection2Questions)
  const [otherQuestionsToShow, setOtherQuestionsToShow] = useState([])

  const setSection2Answers = useStore((state) => state.setSection2Answers)
  const section1Answers = useStore((state) => state.section1Answers)
  const section2Answers = useStore((state) => state.section2Answers)

  const handleChange = (questionIndex) => (item) => {
    if (item) {
      // checks to see if we need to capture other text and whether the user has selected 'Other'
      const isOtherSelected =
        sectionQuestions[questionIndex].captureOtherText &&
        item.selectedItem.showOtherText

      if (isOtherSelected) {
        // user has selected Other so push this question into the tracking array so the text box will appear
        setOtherQuestionsToShow([...otherQuestionsToShow, questionIndex])
      } else {
        // user hasn't selected Other however if Other was selected previously then check and remove it so that the box disappears
        // TODO: may need to remove the answer as well to clear everything
        if (otherQuestionsToShow.includes(questionIndex)) {
          setOtherQuestionsToShow(
            otherQuestionsToShow.filter((ind) => ind !== questionIndex)
          )
        }
      }

      setSection2Answers(
        {
          item: item.selectedItem,
          key: sectionQuestions[questionIndex].key,
        },
        questionIndex
      )

      if (item.selectedItem.showQuestion) {
        if (item.selectedItem.showQuestion !== 18) {
          showQuestion.push(item.selectedItem.showQuestion)
          setShowQuestion([...showQuestion])
        } else {
          // add one special case for question 15 where qestion 8,9,10 need to be yes
          // before the next question shows
          if (
            section2Answers[10] &&
            section2Answers[10].item === 'Yes' &&
            section2Answers[11] &&
            section2Answers[11].item === 'Yes' &&
            section2Answers[12] &&
            section2Answers[12].item === 'Yes'
          ) {
            showQuestion.push(item.selectedItem.showQuestion)
            setShowQuestion([...showQuestion])
          }
        }
      }
    }
  }

  const handleOtherTextChange = (questionIndex) => (ev) => {
    const text = ev.target.value

    setSection2Answers(
      {
        item: { ...section2Answers[questionIndex].item, text },
        key: sectionQuestions[questionIndex].key,
      },
      questionIndex
    )
  }
  // there is now only one question that hides/shows 
  // the Prinicipal list in section 1 has answers coded to show Qeustion 2 here in Section 2
  // so here we just check the presence of the showQuestion and show question 2 there's no need
  // to read the variable like we did before
  useEffect(() => {
    section1Answers.find(answer => answer.item.showQuestion) && setShowQuestion([1])
  }, [section1Answers])

  return (
    <section>
      <div style={{ display: 'flex', justifyContent: 'space-between'}}>
        <h1>Step 2 of 3: Project fit</h1>
        <span><RedStar/> <i>Required</i></span>
      </div>
      <div className='sub-title'>
        Please enter the details related to your project below. Your answers will help us assess how your project fits with the Innovation Fund
scope and expectations.
      </div>
      {
        section2Answers.filter(x=>!x).length > 0 && <div className="required-field">Fields marked with <RedStar /> are required</div>
      }
      {sectionQuestions.map(({ key, ...question }, questionIndex) => {
        return (
          <Question
            {...question}
            key={`${key}-${questionIndex}`}
            questionIndex={questionIndex}
            section={section}
            handleChange={handleChange(questionIndex)}
            handleOther={handleOtherTextChange(questionIndex)}
            showQuestion={
              showQuestion.includes(questionIndex) ? questionIndex : -1
            }
            showOther={
              section2Answers[questionIndex] &&
              section2Answers[questionIndex].item.showOtherText
            }
            required={
              requiredQuestions.filter((rq) => rq === questionIndex)[0] ===
              questionIndex
            }
            initialSelectedItem={section2Answers[questionIndex]?.item || ''}
            autoFocus={questionIndex === 0}
            answered={!!section2Answers[questionIndex]}
          />
        )
      })}
    </section>
  )
}

export default SectionTwo
