import questions from '../data/questions'

const initForm = async () => {
  const postString = questions
    .flat()
    .map((q) => q.key)
    .join('=defaultValue&')

  const initFormId = localStorage['initialise_form_id']
  // post this
  const postStringWithFormId = encodeURI(
    `${postString}=defaultValue&form_tools_form_id=${initFormId}&form_tools_initialize_form=1`
  )
  const url = 'https://webtools.ec.europa.eu/form-tools/process.php'

  console.log('We have posted this ->', postStringWithFormId)

  const response = await fetch(url, {
    method: 'POST',
    mode: 'no-cors',
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
    },
    body: postStringWithFormId,
  })
  console.log('Response from the post ->', response)
}

export default initForm
