import Button from '../components/button'
import { useStore } from '../App'
import findUnansweredQuestions from '../utils/findUndefined'
import showRequiredMessage from '../utils/showRequiredMessage'

const SectionOneButtons = ({ setVisibleSection, visibleSection }) => {
  const section1Answers = useStore((state) => state.section1Answers)
  const setRequiredQuestions = useStore((state) => state.setRequiredQuestions)

  return (
    <>
      <Button
        icon="previous"
        variant="outline"
        onClick={() => {
          setVisibleSection(visibleSection - 1)
        }}
      >
        Back
      </Button>
      <Button
        icon="next"
        onClick={() => {
          const unansweredQuestions = findUnansweredQuestions(section1Answers)
          unansweredQuestions.length > 0
            ? showRequiredMessage() && setRequiredQuestions(unansweredQuestions)
            : setVisibleSection(visibleSection + 1)
        }}
      >
        Step 2: Project fit
      </Button>
    </>
  )
}

export default SectionOneButtons
