import { useSelect } from 'downshift'
import cn from 'classnames'
import styles from './dropdown.module.css'
import ChevronDown from '../chevron-down'
import { useEffect, useState } from 'react'


const reducedText = item =>item.length > 50 ? `${item.substr(0, 50)}...` : item 

export default function DropdownSelect({
  id,
  section,
  question,
  subText,
  subTextWithLink,
  showMore,
  items,
  handleChange,
  answered,
  initialSelectedItem,
  autoFocus
}) {
  const itemToString = (item) => item.text || item

  const {
    isOpen,
    selectedItem,
    getToggleButtonProps,
    getLabelProps,
    getMenuProps,
    highlightedIndex,
    getItemProps,
  } = useSelect({ items, itemToString, onSelectedItemChange: handleChange, selectedItem: initialSelectedItem })

  const [hidden, setHidden] = useState(true)
  const [thisQuestionRequired, setThisQuestionRequired] = useState(true)
  
  useEffect(() => {
    if (answered) {
      setThisQuestionRequired(false)
      return
    } 
    isOpen ? setThisQuestionRequired(false) : setThisQuestionRequired(true)
  }, [isOpen, answered])

  return (
    <div id={`${section}-${id}`} className={styles.wrapper}>
      <label {...getLabelProps()} className={styles.label}>
        <span>
          {question}
          {thisQuestionRequired && (
            <span className={styles.requiredMarker} aria-label="required">
              &nbsp;*
            </span>
          )}
        </span>
        {subText && <span className={styles.labelSubtext}>{subText}</span>}
        {subTextWithLink && ( 
          <>
            {!showMore ? 
              <span className={styles.subTextWithLink} dangerouslySetInnerHTML={{__html: subTextWithLink.text}}></span>
              : 
              <>
                <span className={hidden ? styles.subTextWithLinkHidden : styles.subTextWithLink} dangerouslySetInnerHTML={{__html: subTextWithLink.text}}></span>
                <button className={styles.moreInfo} onClick={()=>setHidden(!hidden)}>{hidden ? `More` : `Less`} Info</button>
              </>
            }
          </>)
        }
      </label>
      <button
        className={thisQuestionRequired ? styles.requiredItem : styles.selectedItem}
        type="button"
        {...getToggleButtonProps()}
        autoFocus={autoFocus}
      >
        {reducedText(selectedItem?.text || selectedItem || 'Choose an item')}
        <span className={thisQuestionRequired ? styles.requiredButton : styles.selectButton}>
          <ChevronDown className={styles.selectButtonIcon} />
        </span>
      </button>
      <ul {...getMenuProps()} className={styles.menu}>
        {isOpen &&
          items.map((itemFromArray, index) => {
            const item = itemFromArray.text || itemFromArray

            return (
              <li
                {...getItemProps({ item, index })}
                key={`${item}-${index}`}
                className={cn(styles.selectItem, {
                  [styles.highlighted]: highlightedIndex === index,
                })}
              >
                {item}
              </li>
            )
          })}
      </ul>
      {thisQuestionRequired && (
        <span className={styles.requiredMarker} aria-label="required">
          * This field is required
        </span>
      )}

      {/* if you Tab from menu, focus goes on button, and it shouldn't. only happens here. */}
      <div tabIndex="0" />
    </div>
  )
}
