import './App.css'
import { useState, useEffect } from 'react'
import create from 'zustand'
import Button from './components/button'
import questions from './data/questions'
import pageConfig from './data/pageConfig'
import StartPage from './components/startPage'
import SummaryPage from './components/summaryPage'
import PageWrapper from './pages/PageWrapper'
import ButtonWrapper from './pages/ButtonWrapper'
import SectionOneButtons from './pages/SectionOneButtons'
import SectionTwoButtons from './pages/SectionTwoButtons'
import SectionThreeButtons from './pages/SectionThreeButtons'
import SummaryButtons from './pages/SummaryButtons'
import SectionOne from './pages/SectionOne'
import SectionTwo from './pages/SectionTwo'
import SectionThree from './pages/SectionThree'
import initForm from './utils/initForm'
import Header from './components/header'
import Footer from './components/footer'

export const storage = () => {
  if (!(typeof window.localStorage === "undefined")) return window.localStorage;
  else if (!(typeof localStorage === "undefined")) return localStorage;
  else return false;
};

const getLocalStorage = (key) => storage() && JSON.parse(window.localStorage.getItem(key));
const setLocalStorage = (key, value) => storage() &&
  window.localStorage.setItem(key, JSON.stringify(value));

export const useStore = create((set) => ({
  visiblePage: -1,
  section1Answers: getLocalStorage("section1Answers")?.section1Answers || [],
  section2Answers: getLocalStorage("section2Answers")?.section1Answers || [],
  section3Answers: getLocalStorage("section3Answers")?.section1Answers || [],
  requiredQuestions: [],
  requiredSection2Questions: [],
  requiredSection3Questions: [],
  initAnswers: (item1, item2, item3) =>
    set((state) => ({
      section1Answers: [...item1],
      section2Answers: [...item2],
      section3Answers: [...item3],
    })),
  setSection1Answers: (item, index) =>
    set((state) => {
      state.section1Answers[index] = item
      const answers = { section1Answers: [...state.section1Answers] }
      setLocalStorage("section1Answers", answers)
      return answers
    }),
  setSection2Answers: (item, index) =>
    set((state) => {
      state.section2Answers[index] = item
      const answers = { section2Answers: [...state.section2Answers] }
      setLocalStorage("section2Answers", answers)
      return answers
    }),
  setSection3Answers: (item, index) =>
    set((state) => {
      state.section3Answers[index] = item
      const answers = { section3Answers: [...state.section3Answers] }
      setLocalStorage("section3Answers", answers)
      return answers
    }),
  setRequiredQuestions: (item) =>
    set((state) => ({
      requiredQuestions: [...item],
    })),
  setRequiredSection2Questions: (item) =>
    set((state) => ({
      requiredSection2Questions: [...item],
    })),
  setRequiredSection3Questions: (item) =>
    set((state) => ({
      requiredSection3Questions: [...item],
    })),
}))

function App() {

  // DEFAULT_SECTION is the index of the visible sections (it's zero indexed)
  // 0 - start page
  // 1 - section 1
  // 2 - section 2
  // 3 - section 3
  // 4 - results/summary page
  const DEFAULT_SECTION = 0
  const [visibleSection, setVSection] = useState(DEFAULT_SECTION)

  const setVisibleSection = (section) => {
    window.scrollTo(0,0)
    setVSection(section)
  }

  const initAnswers = useStore((state) => state.initAnswers)

  useEffect(() => {
    const section1Questions = questions[0]
    const section2Questions = questions[1]
    const section3Questions = questions[2].filter((q) => !q.hidden)

    // initialise answers to undefined so that we have a slot to save them
    const initAnswers1 = getLocalStorage('section1Answers')?.section1Answers ||  section1Questions.map(() => undefined)
    const initAnswers2 = getLocalStorage('section2Answers')?.section2Answers ||  section2Questions.map(() => undefined)
    const initAnswers3 = getLocalStorage('section3Answers')?.section3Answers ||  section3Questions.map(() => undefined)
    initAnswers(initAnswers1, initAnswers2, initAnswers3)
  }, [initAnswers])

  let localInitialiseForm = false
  if (storage()) {
    localInitialiseForm = localStorage['initialise_form_id']
  } 
  return (
    <div className="App">
      {localInitialiseForm && (
        <button onClick={initForm}>Initialise</button>
      )}
      <Header />
      <PageWrapper pageIndex={visibleSection} {...pageConfig[visibleSection]}>
        <StartPage />
        <SectionOne />
        <SectionTwo />
        <SectionThree />
        <SummaryPage />
      </PageWrapper>
      <ButtonWrapper pageIndex={visibleSection}>
        <Button
          icon="next"
          onClick={() => {
            setVisibleSection(visibleSection + 1)
          }}
        >
          Start Questionnaire
        </Button>
        <SectionOneButtons
          setVisibleSection={setVisibleSection}
          visibleSection={visibleSection}
        />
        <SectionTwoButtons
          setVisibleSection={setVisibleSection}
          visibleSection={visibleSection}
        />
        <SectionThreeButtons
          setVisibleSection={setVisibleSection}
          visibleSection={visibleSection}
        />
        <SummaryButtons
          setVisibleSection={setVisibleSection}
          visibleSection={visibleSection}
        />
      </ButtonWrapper>
      <Footer />
    </div>
  )
}

export default App
